
import React, { useState } from 'react'
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            margin: 0,
            padding: 0,
            marginBottom: '5px',
            fontSize: '0.8em',
            alignItems: 'center',
            justifyContent: 'flex-start',
            display: 'flex',
            flexWrap: 'wrap',
            userSelect: 'none'
        },
        p: {
            display: 'flex',
            fontSize: '0.8em',
            padding: '2px 5px',
            margin: 2,
            borderRadius: 2,
            minWidth: 32,
            nowrap: 'true',
            textTransform: 'capitalize'
        },
        text: {
            fontSize: '0.8em',
            lineHeight: '32px',
            padding: theme.spacing(0, 1)
        },
        clearfix: {
            clear: 'both',
        },
        current: {
            display: 'flex',
            flexWrap: 'wrap',
            background: '#0066cc',
            fontSize: '0.8em',
            color: '#fff',
            padding: '2px 5px',
            borderRadius: 2,
            margin: 2,
            minWidth: 32
        },
        skip: {
            float: 'left',
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            marginLeft: theme.spacing(1)
        },
        input: {
            float: 'left',
            padding: '3px 3px',
            border: '1px solid #dedede',
            borderRadius: 3,
            width: 30
        }
    }))
/**
 * 
 * @param props 
 */
const ListPagination = (props: any) => {
    const {
        total,
        page,
        pageSize,
        onPageChange,
        onPageSizeChange,
        maxShow
    } = props
    //  
    const classes = useStyles()
    // 
    const pageTotal = Math.floor(total / pageSize);
    const [newPage, setNewPage] = useState(page)
    const [newPageSize, setNewPageSize] = useState(pageSize)

    // 
    const cal = (): any => {
        let max = maxShow % 2 == 0 ? maxShow + 1 : maxShow;
        let startpage = 1
        let endpage = max;
        let showNum = max;
        let halfNum = (max - 1) / 2; // 

        // stepNum:
        //  - stepNum 1page-halfNum
        if (page - halfNum > 1) {
            startpage = page - halfNum;
        } else {
            startpage = 1;
        }
        //  + stepNum page+halfNum
        if (page + halfNum < pageTotal) {
            endpage = page + halfNum;
        } else {
            endpage = pageTotal;
            // -showNum,showNum-showNum
            startpage = startpage > pageTotal - showNum && pageTotal > showNum ? pageTotal - showNum : startpage;
            // showNumshowNum
            endpage = endpage < showNum && endpage < pageTotal ? showNum : endpage;
            endpage = endpage > pageTotal ? pageTotal : endpage;
        }
        return { startpage, endpage }
    }
    const { startpage, endpage } = cal()
    // 
    const renderNumpages = (props: any) => {

        const { startpage, endpage, page, onPageChange, classes } = props
        const nodes = []

        for (let i = startpage; i <= endpage; i++) {
            nodes.push(<Button
                key={i}
                variant="outlined"
                size="small"
                className={i === page ? classes.current : classes.p}
                onClick={() => {
                    onPageChange(i)
                }}>{i}</Button>)
        }
        return nodes
    }
    return (
        <Box className={classes.root}>
            {total > 0 && (
                <Box className={classes.text}>
                    {total},
                    {page}/{pageTotal < page && total > 0 ? page : pageTotal}.
                </Box>
            )}
            {page > 1 && (<>
                <Button
                    variant="outlined"
                    size="small"
                    className={classes.p}
                    onClick={() => {
                        onPageChange(1)
                    }}></Button>
                <Button
                    variant="outlined"
                    size="small"
                    className={classes.p}
                    onClick={() => {
                        onPageChange(page - 1)
                    }}></Button>
            </>)}
            {startpage > 1 && (<>
                <Button
                    variant="outlined"
                    size="small"
                    className={classes.p}
                    onClick={() => {
                        onPageChange(1)
                    }}>1</Button>
                <Box className={classes.text}>...</Box>
            </>)}
            {pageTotal > 1 && renderNumpages({
                startpage,
                endpage,
                page,
                onPageChange: (i: number) => {
                    onPageChange(i)
                },
                classes
            })}
            {endpage > 1 && endpage < pageTotal && (<>
                <Box className={classes.text}>...</Box>
                <Button
                    variant="outlined"
                    size="small"
                    className={classes.p}
                    onClick={() => {
                        onPageChange(pageTotal)
                    }}>
                    {pageTotal}
                </Button>
            </>)}
            {page < pageTotal && (<>
                <Button
                    variant="outlined"
                    size="small"
                    className={classes.p}
                    onClick={() => {
                        onPageChange(page + 1)
                    }}>
                    </Button>
                <Button
                    variant="outlined"
                    size="small"
                    className={classes.p}
                    onClick={() => {
                        onPageChange(pageTotal)
                    }}>
                    </Button>
            </>)}
            {pageTotal > 1 && (
                <Box className={classes.skip}>
                    <Button
                        variant="outlined"
                        size="small"
                        className={classes.p}
                        onClick={() => {
                            onPageChange(newPage)
                        }}></Button>
                    <Box component="span">
                        <input
                            type="number"
                            value={newPage}
                            className={classes.input}
                            onChange={(event: any) => {
                                if (event.target.value) {
                                    setNewPage(Number(event.target.value))
                                }
                            }}
                        />
                    </Box>
                </Box>
            )}
            {total > 0 && (
                <Box className={classes.skip}>
                    <Button
                        variant="outlined"
                        size="small"
                        className={classes.p}
                        onClick={() => {
                            onPageSizeChange(newPageSize)
                        }}></Button>
                    <Box component="span">
                        <input
                            type="number"
                            value={newPageSize}
                            className={classes.input}
                            onChange={(event: any) => {
                                if (event.target.value) {
                                    setNewPageSize(Number(event.target.value))
                                }
                            }}
                        />
                    </Box>
                </Box>)}
            <Box className={classes.clearfix}></Box>
        </Box>
    )
}
export default ListPagination