import {httpGet,httpPost} from '../api'
import {getConfig} from '../storage/ConfigStorage'
/**
 * 
 */
export const makeAttachProvide = () => {

  const {headers,apiUrls} = getConfig()

  // 
  const getAttachList = (props: any) => {
    const { params, success, failure,url } = props
    httpGet({
      headers,
      url:apiUrls.getAttachList,
      params
    }).then((res: any) => {
      const data = res.data;
      const message = data && data.message
      if (data && data.code === 1) {
        const list = data.result.list
        const pagination = data.result.pagination
        // 
        if (success) {
          success({
            list,
            pagination,
            message: message || ''
          })
        }
      } else {
        // 
        failure(message || '')
      }
    })
  }
  // 
  const getAttachListByUrls = (props: any) => {
    const { urls, success, failure } = props
    httpPost({
      headers,
      url:apiUrls.getAttachListByUrls,
      data:{
        urls
      }
    }).then((res: any) => {
      const data = res.data;
      const message = data && data.message
      if (data && data.code === 1) {
        const list = data.result.list
        // 
        if (success) {
          success(list)
        }
      } else {
        // 
        failure(message || '')
      }
    })
  }

  // 
  const getGroupList = (props: any) => {
    const { success, failure } = props

    httpGet({
      headers,
      url:apiUrls.getGroupList,
    }).then((res: any) => {
      const data = res.data;
      const message = data && data.message
      if (data && data.code === 1) {
        const list = data.result.list
        // 
        if (success) {
          success(list)
        }
      } else {
        // 
        failure(message || '')
      }
    })
  }

  // 
  const moveAttach = (props: any) => {
    const { success, failure, id, groupId } = props
    httpPost({
      headers,
      url:apiUrls.moveAttach,
      data:{id, groupId}
    }).then((res: any) => {
      const data = res.data;
      if (data && data.code === 1) {
        // 
        if (success) {
          success(data.message || '')
        }
      } else {
        // 
        const message = data && data.message
        failure(message || '')
      }
    })
  }

  // 
  const saveGroup = (props: any) => {
    const { success, failure, group } = props
    // redux 
    httpPost({
      headers,
      url:apiUrls.saveGroup,
      data:{
        group
      }
    }).then((res: any) => {
      const data = res.data;
      if (data && data.code === 1) {
        // 
        if (success) {
          success(data.message || '')
        }
      } else {
        // 
        const message = data && data.message
        failure(message || '')
      }
    })
  }

  // 
  const removeGroup = (props: any) => {
    const {success, failure, id } = props
    httpPost({
      headers,
      url:apiUrls.removeGroup,
      data:{
        id
      }
    }).then((res: any) => {
      const data = res.data;
      if (data && data.code === 1) {
        // 
        if (success) {
          success(data.message || '')
        }
      } else {
        // 
        const message = data && data.message
        failure(message || '')
      }
    })
  }

  // 
  const removeAttach = (props: any) => {
    const {success, failure, id } = props
    httpPost({
      headers,
      url:apiUrls.removeAttach,
      data:{id}
    }).then((res: any) => {
      const data = res.data;
      if (data && data.code === 1) {
        // 
        if (success) {
          success(data.message || '')
        }
      } else {
        // 
        const message = data && data.message
        failure(message || '')
      }
    })
  }
  // 
  return {
    getAttachList,
    getAttachListByUrls,
    getGroupList,
    moveAttach,
    saveGroup,
    removeGroup,
    removeAttach
  }
}
