import { setConfig } from '../storage/ConfigStorage'
import { words as defaultWords } from '../config/locale/zh'
export const localeNames = [{
  name: '',
  locale: 'zh'
}, {
  name: 'English',
  locale: 'en'
}];
// 
export const getName = (locale: string) => {
  let name
  localeNames.map(l => {
    if (l.locale === locale) {
      name = l.name
      return;
    }
  })
  return name 
}
interface Result {
  initConfig: Function
}
export const defaultLocaleConfig = {
  localeNames,
  words: { ...defaultWords },
  name: '',
  locale: 'zh'
}
/**
 * 
 */
export const makeConfigProvide = (): Result => {

  // 
  const initConfig = async (props: any) => {
    const {
      headers,
      apiUrls,
      locale,
      attachPrefix
    } = props

    // 
    const { words } = await import(`../config/locale/${locale || 'zh'}`)
    const value = words || defaultWords
    // 
    const name = () => getName(locale)
    const localeConfig = {
      localeNames,
      words: { ...value },
      locale,
      name
    }
    const config = {
      headers,
      apiUrls,
      localeConfig,
      attachPrefix
    }
    setConfig(config)
  }
  return {
    initConfig
  }
}